import React, { useState } from 'react'
import Chart from "react-apexcharts";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from 'moment'
import {useParams}from 'react-router-dom'
import { useEffect } from 'react';
const Dashboard = () => {
  const [access_token, setAccess_token] = useState(localStorage.getItem('access_token'))
  const [totalEnergy, setTotalEnergy] = useState(0)
  const [deviceName, setDeviceName] = useState('')
    const { device_id,device_name } = useParams();
    const [selectedMonth, setSelectedMonth] = useState(moment().format('MMMM'));
  
    const [powerConsumption, setPowerConsumption] = useState({
        series: [{
        name: 'Energy (in KWh)',
        data: [44, 55, 41, 37, 22, 43, 21]
      }],
      options: {
        chart: {
          type: 'bar',
          height: 350,
          stacked: false,
        },
        plotOptions: {
          bar: {
            horizontal: false,
            dataLabels: {
              total: {
                enabled: true,
                offsetX: 0,
                style: {
                  fontSize: '13px',
                  fontWeight: 900
                }
              }
            }
          },
        },
        stroke: {
          width: 1,
          colors: ['#fff']
        },
        title: {
          text:device_name
        },
        xaxis: {
        //   categories: [2, 3, 10, 11, 12, 1, 4,2, 3, 10, 11, 12, 1, 4,2, 3, 10, 11, 12, 1, 4],
          labels: {
            formatter: function (val) {
              return val + "AM"
            }
          }
        },
        yaxis: {
          title: {
            text: undefined
          },
        },
        tooltip: {
          y: {
            formatter: function (val) {
              return val + "K"
            }
          }
        },
        fill: {
          opacity: 1
        },
        colors: ["#FFA500"],
        legend: {
          position: 'top',
          horizontalAlign: 'left',
          offsetX: 40
        }
      }
      })
      const months = [
        'January', 'February', 'March', 'April',
        'May', 'June', 'July', 'August',
        'September', 'October', 'November', 'December'
      ];
    
      const handleMonthChange = (e) => {
        setSelectedMonth(e.target.value);
      };
useEffect(()=>{
    setDeviceName(device_name)
    const currentDate = moment();
    const currentDay = currentDate.date();
    const currentMonth = moment().month(selectedMonth).month() + 1; // Months are zero-indexed, so add 1
    const currentYear = currentDate.year();

    const payload = {
        method: "POST",
        headers: {
          Authorization: `Bearer ${access_token}`,
          'Content-Type': 'application/json'
          
        },
        body: JSON.stringify({
          intent: "action.entities.ENERGY_DATA",
          payload: {
            device_ids:[device_id],
            // monthly count
            duration: "month",
            month:currentMonth,
            year: currentYear 
            // custom date range
            // [ duration: "custom",
            // timeFrom:1709251200000,
            // timeTo:1717200000000 ]

            // daya whose count
            // duration: "day",
            // startDay: currentDay,
            // group_by_minutes: 15,
            // filter_slot: {hour: 0, minute: 0},
            // month:currentMonth,
            // year: currentYear,
            // api_v: "1(default)/2/3"

          }
        })
      };
      
      fetch('https://partner.feturtles.com/integrations/ctp/go', payload)
        .then(response => response.json())
        .then(data =>{
            console.log('data',data)
            let avgEnergyArray = [];
            var avgEnergyLabels=[]
            let totalSum = 0;
            var sortedData=data.sort((a, b) => a._id - b._id)
            sortedData.forEach(obj => {
     avgEnergyArray.push((obj.avg_energy/3600000).toFixed(2));
     totalSum += obj.avg_energy;
     avgEnergyLabels.push(obj._id)
      });
      console.log('avgEnergyArray',avgEnergyArray);
      setPowerConsumption(prevState => ({
        ...prevState,
        series: [{
          ...prevState.series[0], // Keep other properties of series object intact
          data: avgEnergyArray // Update the data field array
        }],
        options: {
          ...prevState.options,
          xaxis: {
            ...prevState.options.xaxis,
            categories: avgEnergyLabels, // Update categories with new labels
            labels: {
              formatter: function (val) {
                return "Day "+val  ; // Optionally update label formatting
              }
            }
          }
        }
      
      }));
      setTotalEnergy((totalSum/3600000).toFixed(2))

        }
        )
        .catch(error => console.error('Error:---', error));

},[selectedMonth])
  return (
    <div class="mr-4 sm:ml-52">
         <div className='shadow-lg border-2 border-gray-200 p-3'>
      <div className='flex flex-row justify-between items-center'>
      <div className="flex flex-col items-center my-2 ">
      <label htmlFor="monthSelect" className="mb-2 font-bold">Select a month:</label>
      <select
        id="monthSelect"
        value={selectedMonth}
        onChange={handleMonthChange}
         className="p-2 border border-gray-300 rounded-md focus:outline-none focus:border-blue-500"
      >
        <option value="" hidden>-- Select Month --</option>
       {months.map((month, index) => (
          <option key={index} value={month}>{month}</option>
        ))}
      </select>
      
    </div>
    <p className='text-xl font-normal mx-3'>Total Energy : <span className='text-orange-500 text-lg font-medium'>{totalEnergy} Kwh</span></p>
    </div>
   <div id="column-chart mx-3 ml-5 ">
    <Chart
            options={powerConsumption.options}
            series={powerConsumption.series}
            type="bar"
            width="90%"
            className=""
          />
           <div className='w-full flex flex-row justify-between items-center px-3 bg-red-50 p-2 rounded-md mb-2'>
          <div  className='flex-auto'>
    <h1 className='text-base text-gray-700'>Energy Consumption</h1>
   <h1 className='text-lg font-bold text-gray-700'>{totalEnergy} kWh </h1>

   </div>
   <div  className='flex-auto'>
    <h1 className='text-base text-gray-700'>Cost Include</h1>
   <h1 className='text-llg font-bold text-gray-700'>{totalEnergy*2} INR</h1>

   </div>
   <div className='flex-auto'>
    <h1 className='text-base text-gray-700'>Run time</h1>
    <div className="w-full bg-gray-200 rounded-full h-2.5 mb-4 dark:bg-gray-700">
  <div className="bg-yellow-400 h-2.5 rounded-full" style={{width:'45%'}}></div>
</div>
   </div>
          </div>
  </div>
  </div>

  






    </div>
  )
}

export default Dashboard