import React from 'react'
import { Link } from 'react-router-dom'

const Navbar = () => {
  return (
    <div>
          <aside id="default-sidebar" class="fixed top-0 left-0 z-40 w-48 h-screen transition-transform -translate-x-full sm:translate-x-0" aria-label="Sidebar">
   <div class="h-full px-3 py-2 shadow-lg  border-2 border-gray-200  overflow-y-auto bg-gray-50 ">
      <ul class="space-y-2 font-medium">
         <li>
            <Link to={'/'} class="flex border-4 border-gray-200 flex-col items-center p-2 py-4 text-gray-900 rounded-lg hover:bg-orange-200 focus:bg-orange-300 group active:bg-gray-100">
              <img  src={require('../assets/home.png') } class="w-12 object-contain text-orange-700 transition duration-750 group-hover:text-gray-900 "/>
               <span class="ms-3">Dashboard</span>
            </Link>
         </li>
         <li>
            <Link  to={'/Login'} class="flex border-4 border-gray-200 flex-col items-center p-2 py-4 text-gray-900 rounded-lg  dark:hover:bg-orange-200 focus:bg-orange-300  group">
            <img  src={require('../assets/users.png') } class="w-12 object-contain text-orange-700 transition duration-750 group-hover:text-gray-900 "/>

               <span class="flex-1 ms-3 whitespace-nowrap">Users</span>
            </Link>
         </li>
         <li>
            <a href="#" class="flex border-4 border-gray-200 flex-col items-center p-2 py-4 text-gray-900 rounded-lg  hover:bg-gray-100 dark:hover:bg-orange-200 focus:bg-orange-300 group">
            <img  src={require('../assets/remote.png') } class="w-12 object-contain text-orange-700 transition duration-750 group-hover:text-gray-900 "/>
               <span class="flex-1 ms-3 whitespace-nowrap">Devices</span>
            </a>
         </li>
         <li>
            <a href="#" class="flex border-4 border-gray-200 flex-col items-center p-2 py-4 text-gray-900 rounded-lg  hover:bg-gray-100 dark:hover:bg-orange-200 focus:bg-orange-300 group">
            <img  src={require('../assets/time.png') } class="w-12 object-contain text-orange-700 transition duration-750 group-hover:text-gray-900 "/>

               <span class="flex-1 ms-3 whitespace-nowrap">Schedules</span>
            </a>
         </li>
         <li>
            <a href="#" class="flex border-4 border-gray-200 flex-col items-center p-2 py-4 text-gray-900 rounded-lg  hover:bg-gray-100 dark:hover:bg-orange-200 focus:bg-orange-300 group">
            <img  src={require('../assets/remote.png') } class="w-12 object-contain text-orange-700 transition duration-750 group-hover:text-gray-900 "/>

               <span class="flex-1 ms-3 whitespace-nowrap">Remotes</span>
            </a>
         </li>
         <li>
            <a href="#" class="flex border-4  flex-col items-center p-2 py-4 text-gray-900 rounded-lg  hover:bg-gray-100 dark:hover:bg-orange-200 focus:bg-orange-300 group">
            <img  src={require('../assets/analytics.png') } class="w-12 object-contain text-orange-700 transition duration-750 group-hover:text-gray-900 "/>
               <span class="flex-1 ms-3 whitespace-nowrap">Analytics</span>
            </a>
         </li>
         
      </ul>
   </div>
</aside>
    </div>
  )
}

export default Navbar